body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.searchCol {
	margin: 0 200px;
}

.resultsCol,
.singleResult {
	border: 1px solid #e0dede;
	border-radius: 3px;
	padding: 10px;
	background-color: #f7f7f7;
}

ul {
	list-style-type: none;
}

.singleResult {
	background-color: white;
}

h3 {
	font-family: 'Mukta', sans-serif;
	color: #969797;
	/* margin: 10px; */
}

.pic {
	float: left;
	margin: 1vh 1vw;
}

p {
	font-size: 1rem;
	line-height: 180%;
	font-family: 'Lato', 'Open Sans';
	padding: 0vh 10vw 2vh 10vw;
	text-align: justify;
}

.navLinks {
	margin-right: 10px;
	text-decoration: none;
	font-size: 1.5rem;
	color: #969797;
	margin-top: 6px;
}

.navLinks,
.navLogo {
	font-family: 'Mukta', sans-serif;
}

.jumboBg {
	background-image: url(/static/media/jumbotronPic.5f5ce4bf.jpg);
	position: relative;
	opacity: .75;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	min-height: 30vh;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

